<template>
  <div class="page-list">

    <mt-header title="热门岗位">
      <router-link to="/" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header>

    <nav class="tab-city">
      <mt-button @click.native="popupVisible1 = true" size="large" ref="button">浙江省</mt-button>
      <mt-button @click.native="popupVisible2 = true" size="large" ref="button">杭州市</mt-button>
      <mt-popup v-model="popupVisible1" popup-transition="popup-fade" class="mint-popup" :style="{ top: buttonBottom + 'px' }">
        <ul>
          <li>安徽省</li>
          <li>江苏省</li>
          <li class="active">浙江省</li>
          <li>北京市</li>
          <li>江西省</li>
          <li>湖北省</li>
          <li>湖南省</li>
        </ul>
      </mt-popup>
      <mt-popup v-model="popupVisible2" popup-transition="popup-fade" class="mint-popup" :style="{ top: buttonBottom + 'px' }">
        <ul>
          <li>杭州市</li>
          <li>温州市</li>
          <li class="active">台州市</li>
          <li>丽水市</li>
          <li>湖州市</li>
          <li>嘉兴市</li>
        </ul>
      </mt-popup>
    </nav>

    <div class="list-infinite-wrapper" ref="wrapper" :style="{ height: wrapperHeight + 'px' }">
      <ul class="list-infinite-list" v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="50">
        <job-item 
              v-for="(item, index) in list"
              key="index"
              href="part_time_post?id=123"
              title="兼职展会礼仪迎宾接待"
              salary="380" 
              location="杭州"
              time="5月25日起工作（共35天）" />
      </ul>
      <p v-show="loading" class="list-infinite-loading">
        <mt-spinner type="fading-circle"></mt-spinner>
        加载中...
      </p>
    </div>
    
  </div>
</template>

<script>
import commonTitle from '../common/title.vue'
import jobItem from '../common/jobItem.vue'

export default {
  name: 'page-list',

  components: {
      commonTitle,
      jobItem
  },

  data() {
      return {
        popupVisible1: false,
        popupVisible2: false,
        buttonBottom: 0,
        list: [],
        loading: false,
        allLoaded: false,
        wrapperHeight: 0
      };
    },

    methods: {
      loadMore() {
        this.loading = true;
        setTimeout(() => {

          let last = this.list[this.list.length - 1];
          for (let i = 1; i <= 10; i++) {
            this.list.push("http://www.cssoul.com/demo/weihai/company/12.jpg");
          }
          this.loading = false;
        }, 1000);
      }
    },

    created() {
      for (let i = 1; i <= 10; i++) {
        this.list.push(i);
      }
    },

    mounted() {
      this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top;

      this.buttonBottom = this.$refs.button.$el.getBoundingClientRect().bottom+1;
    }
};
</script>

<style>
  .tab-city{
    display:flex;
  }
  .tab-city .mint-button {
    flex:1;
    background:#fff;
    border-radius:0;
  }
  .tab-city .v-modal{
    top: 86px;
  }

  .mint-popup{
    width: 100%;
    height: 250px;
    padding: 0;
    transform: translate(-50%, 0);
    overflow:scroll;
  }
  .mint-popup ul{
    list-style:none;
    margin:10px 0;
    padding:0 20px;
  } 
  .mint-popup ul li{
    height:40px;
    line-height:40px;
    color:#828282;
  }
  .mint-popup ul .active{
    color: #0977C5;
  }


  .list-infinite-wrapper {
    margin-top: 1px;
    overflow: scroll;
  }
  
  .list-infinite-loading {
    text-align: center;
    height: 50px;
    line-height: 50px;
  }
  .list-infinite-loading div {
    display: inline-block;
    vertical-align: middle;
    margin-right: 5px;
  }
  
</style>



